
#app {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 100vh;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
html {
  background-color: #478CBF;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
*, *:before, *:after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}
.disable {
  display: none !important;
}
.select {
  overflow: hidden;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  font-family: sans-serif;
  height: 100%;
  width: 12em;
  -webkit-transition: width .2s;
  transition: width .2s;
}
.select a {
    text-decoration: none;
    font-weight: 400;
    color: white;
    display: block;
    text-align: left;
    width: 100%;
    padding: 2em;
}
.select a:visited {
    color: white;
}
.select a:hover {
    color: #32C994;
}
.hide {
  width: 0;
}
.hide #menuToggle {
    position: absolute;
    right: 0;
    display: inline;
    width: auto;
}
body {
  padding: 0;
  margin: 0;
}
#anim {
  width: 100%;
  height: 100%;
  overflow: hidden;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}



